import React from 'react';
import {showAlert} from '../Alert';
import SubmitDataHelpers from '../helpers/SubmitDataHelpers.js';
import CookieHelpers from '../helpers/CookieHelpers';

import CreateCourseStyle from '../../css/courses/CreateCourseStyle';
import '../../css/courses/CreateCourse.css';

class CreateCourse extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            course: {
                password: Math.floor(Math.random()*(10000-1000)) + 1000,
            }
        };
        this.handleSubmit = this.handleSubmit.bind(this);
        this.removeModal = this.removeModal.bind(this);
        this.clickedForm = this.clickedForm.bind(this);
    }

    componentDidMount(){
        document.getElementById("popUpModal").onclick = this.removeModal;
        CreateCourseStyle();
    }

    componentDidUpdate(){
        CreateCourseStyle();
    }

    handleSubmit(e){
        e.preventDefault();
        
        let form = e.target;
        
        let error = '';

        if(form['password'].value.length < 4){
            error = "Access key must have a length of 4";
        } else if(form['type'].value == ''){
            error = "Course type must be Line, Scatter or Score";
        } else if(form['startTime'].value >= form['endTime'].value){
            error = 'Course cannot end before start.';
        }

        if(error === ''){
            let helpers = new SubmitDataHelpers;
            console.log(this.state.event);
            helpers.postData('/courses/createcourse/', 
            {
                password: form['password'].value,
                type: form['type'].value,
                event: form['event'].value,
                startTime: form['startTime'].value,
                endTime: form['endTime'].value,
                status: 'Coming',
                checkPointNo: form['checkPointNo'].value,
                firstCheckPoint: (Math.round(Math.random() * 10)),
            }).then((res) => {
                console.log(res); // JSON data parsed by `response.json()` call
                if(res.error){
                    console.log(res.error);
                    showAlert(res.error, 5000);

                    this.setState({...this.state, ['error']: res.error}); 
                } else {
                    /*Add creator to course participant after course create*/
                    let cookieHelpers = new CookieHelpers;
                    
                    helpers.postData('/participants/addParticipant',
                    {
                        eventName: form['event'].value,
                        courseType: form['type'].value,
                        parName: cookieHelpers.getCookie("username"),
                    }).then(res => {
                        if(!res.error){
                            window.location.href = "/events/" + form['event'].value;
                        } else {
                            showAlert(error, 5000);

                            this.setState({...this.state, ['error']: error}); 
                        }
                    });

                }
            });
        } else {
            showAlert(error, 5000);

            this.setState({...this.state, ['error']: error}); 
        }
    }

    render(){
        return(
            <div id="newCourse" className="newCourse card shadow-lg pt-4" onMouseDown={this.clickedForm} onMouseUp={this.clickedForm}>
                <h2 className="card-title mt-3">New course</h2>
                <form id="newCourseForm" className="card-body px-0" autoComplete="off" onSubmit={this.handleSubmit}>
                    <input className="input px-2 py-1 mb-2" 
                        type="text" name="event" value={window.sessionStorage.getItem("eventName")}  disabled 
                    /><br/>
                    <select className="input px-2 py-1 my-1" name="type">
                        <option defaultValue="">Choose a course</option>
                        <option value="Line">Line</option>
                        <option value="Scatter">Scatter</option>
                        <option value="Score">Score</option>
                    </select>
                    <br/>
                    <input className="input px-2 py-1 my-1" 
                        type="text" name="password" value = {this.state.course.password} 
                        onChange={e => this.setState({...this.state, ['course']:{password: e.target.value}})} 
                        maxLength="4" placeholder="Access key (4 characters only)..." required 
                    /><br/>
                    <input className="input px-2 py-1 my-1" 
                        type="text" name="startTime" onMouseDown={e => e.target.type='time'} 
                        placeholder="Start time..." required 
                    /><br/>
                    <input className="input px-2 py-1 my-1" 
                        type="text" name="endTime" onMouseDown={e => e.target.type='time'} 
                        placeholder="End time..." required 
                    /><br/>
                    <input className="input px-2 py-1 my-2" 
                        type="number" name="checkPointNo" 
                        placeholder="Enter number of checkpoints" min="0" required 
                    /><br/>
                    <input className="button btn mt-3" type="submit" value="Submit" />
                </form>
            </div>
        );
    }

    clickedForm(){
        this.setState({...this.state, ['clickedForm']: true});
        //console.log(this.state.clickedForm);
    }

    removeModal(){
        //console.log(this.state.clickedForm);
        if(!this.state.clickedForm){
            document.getElementById("popUpModal").remove();
        } else{
            this.setState({...this.state, ['clickedForm']: null});
        }
    }
}

export default CreateCourse;